@use 'scss/tokens/spacing';
@use 'sauce/prop';

:root {
  @include prop.define('spacing-ant',       spacing.$ant);
  @include prop.define('spacing-bee',       spacing.$bee);
  @include prop.define('spacing-chaffinch', spacing.$chaffinch);
  @include prop.define('spacing-dog',       spacing.$dog);
  @include prop.define('spacing-eagle',     spacing.$eagle);
  @include prop.define('spacing-flamingo',  spacing.$flamingo);
  @include prop.define('spacing-grizzly',   spacing.$grizzly);
  @include prop.define('spacing-hippo',     spacing.$hippo);
  @include prop.define('spacing-iguanadon', spacing.$iguanadon);
}
