@use 'scss/tokens/type';
@use 'sauce/prop';

:root {
  @include prop.define('type-rollerskate', type.$rollerskate);
  @include prop.define('type-skateboard',  type.$skateboard);
  @include prop.define('type-scooter',     type.$scooter);
  @include prop.define('type-bike',        type.$bike);
  @include prop.define('type-moped',       type.$moped);
  @include prop.define('type-car',         type.$car);
  @include prop.define('type-van',         type.$van);
}
